
header {
   height: 50px;
   color: #333;
   font-size: 18px;
   width: 100%;
   text-align: center;
   line-height: 50px;
   position: relative;
   margin-bottom: 20px;
}
.glyphicon-chevron-left {
   position: absolute;
   left: 5%;
   top:15px;
}
.login-list {
   padding: 2px 5%;
}
.login-list li {
   list-style: none;
   height: 44px;
   padding: 0 15px;
   margin: 5px 0;
}
.input-box {
   border: 1px solid #f1f1f1;
   border-radius: 25px;
   height: 90%;
   line-height: 90%;
   position: relative;
}
.input-box label {
   height: 44px;
   margin: 5px 0;
}
.input-box input {
   border: 1px solid #f1f1f1;
   border-radius: 25px 0 0 25px;
   border-right: 0;
   height: 90%;
   width: 150%;
   line-height: 90%;
   position: relative;
   top:-6px;
   font-weight: normal;
   outline: none;
   *outline: none;
   padding-left: 20px;
   color: #333;
}
label span{
   color: #333;
   font-weight: normal;
}
span.country-name,span.tel-num, span.get-psw {
   position: relative;
   top:12px;
   padding-left: 20px;
   font-size: 16px;
}
span.tel-num, span.get-psw, span.password {
   color: #ccc;
}
.fr {
   position: absolute;
   right: 0;
   top:10px;
   right: 35px;
}
.input-box .fr::after {
   content: '';
 /*  height: 0;*/
   border-left: 5px solid transparent;
   border-right: 5px solid transparent;
   border-top: 5px solid #666;
   position:absolute;
   top:4px;
   margin-left: 5px;
}
.password {
   position: absolute;
   right: 0;
   top:10px;
   right: 20px;
}
.login-input-box {
   display: block;
   height: 100%;
   background-color: #2692ff;
   border-radius: 25px;
}
.login-input-box a {
   display: block;
   height: 100%;
   text-align: center;
   color: #fff;
   font-size: 18px;
}
span.login-btn {
   position: relative;
   top:10px;
}
.way-input-box {
   display: block;
   height: 100%;
   background-color: #fff;
   border-radius: 25px; 
   border: 1px solid #2692ff;
}
.choose-input-box span {
   display: block;
   text-align: center;
   color: #ccc;
}
.way-input-box a {
   display: block;
   height: 100%;
   text-align: center;
   color: #2692ff;
   font-size: 18px;
}
footer {
   position: absolute;
   bottom: 0;
   width: 100%;
   height: 118px;
}
.login-api ul {
   padding: 0 20px 0 40px;
}
.login-api ul li {
   display: inline-block;
   width: 22%;
   text-align: center;
}
.remind-new {
   text-align: center;
}
span.no-count {
   color: #c5c9d2;
}
.remind-new span a {
   color:#666;
}
a {
   text-decoration: none;
}



