
.drop-enter-active[data-v-2c06aa00] {
  transition: all .5s ease;
}
.drop-leave-active[data-v-2c06aa00] {
  transition: all .3s ease;
}
.drop-enter[data-v-2c06aa00] {
  -webkit-transform: translateY(-500px);
          transform: translateY(-500px);
}
.drop-leave-active[data-v-2c06aa00] {
  -webkit-transform: translateY(-500px);
          transform: translateY(-500px);
}
.dialog-wrap[data-v-2c06aa00] {
/*  position: fixed;*/
  width: 100%;
  height: 100%;
}
.dialog-cover[data-v-2c06aa00] {
  background: #000;
  opacity: .3;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dialog-content[data-v-2c06aa00] {
  position: fixed;
  overflow: auto;
  background: #fff;
  z-index: 10;
  bottom: 0;
  width: 100%;
  height: 330px;
}



