






























































































































































/*头部开始*/
.detail-header[data-v-df6a4b46] {
    height: 44px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    border-bottom: 1px solid #e6e6e6;
    background-color: #fff;
}
.header-menu-icon[data-v-df6a4b46] {
    float: left;
    background: url(http://m.youku.com/video/images/youkunav.png?ver=1499329066389) no-repeat 0px -20px;
    width: 40px;
    height: 30px;
    background-size: 375px 200px;
}
.header-logo-icon[data-v-df6a4b46] {
    position: relative;
    width: 40%;
    top: -5px;
    margin-left: 30%;
    height: 44px;
    float: left;
    background-color: red;
   /* margin-left: 110px;*/
    background: url(http://m.youku.com/video/images/youkunav.png?ver=1499329066389) no-repeat 0px 20px; 
    background-size: 375px 200px;
}
.header-box-right[data-v-df6a4b46] {
    float: right;
}
.header-box-right[data-v-df6a4b46] {
    left: -30px;
}
.header-box-right span[data-v-df6a4b46] {
  /*  display: block;*/
}
.header-search-icon[data-v-df6a4b46] {
    width: 44px;
    height: 44px;
    background: url(http://m.youku.com/video/images/youkunav.png?ver=1499329066389) no-repeat 0px 60px;
}
 /* .header-user-icon {
    width: 44px;
    height: 44px;
    background: url(http://m.youku.com/video/images/youkunav.png?ver=1499329066389) no-repeat 0px 60px;
  }*/
/*头部结束*/

/*轮播图*/
.open-window[data-v-df6a4b46] {
  height: 66px;
  background-color: #fff; 
  width: 100%;
  top: 45px;
  position: relative;
}
.close[data-v-df6a4b46] {
    background-color: #fff;
    width: 30px;
    height: 30px;
    background:url(http://m.youku.com/video/images/close.png) no-repeat;
    background-size: 50%;
    margin-top: 20px;
    cursor: pointer;
    margin-left: -10px;
}
.right-content a[data-v-df6a4b46] {
    width: 100%;
    height: 66px;
    color:#001;
    font-size: 14px;
}
.right-content a img[data-v-df6a4b46]{
    width: 40px;
    height: 40px;
    margin-top: 10px;
    margin-left: -30px;
}
.right-content span[data-v-df6a4b46] {
    left: 10px;
    top:5px;
    position: relative;
}
.open-btn a[data-v-df6a4b46]{
    display: block;
    width: 44px;
    background-color: #2692ff;
    height: 28px;
    text-align: center;
    line-height: 28px;
    border-radius: 4px;
    margin-left: -5px;
    margin-top: 15px;
    color: #fff;
}
/*轮播图结束*/

/*播放*/
.detail-video[data-v-df6a4b46] {
    position: relative;
    width: 100%;
    height: 250px;
    background-color: rgba(0,0,0,.8);
    top: 30px;
}
.open-videoapp[data-v-df6a4b46] {
    position: relative;
    height: 100px;
    border-bottom: 1px solid #e6e6e6;
}
.open-box[data-v-df6a4b46] {
    margin-top: 45px;
    border: 1px solid #2692ff;
    height: 45px;
    line-height: 45px;
    border-radius: 30px;
    text-align: center;
    font-size: 16px;
    color: #2692ff;
}
.open-videoapp a[data-v-df6a4b46] {
    text-decoration: none;
}
.btn-full-version a[data-v-df6a4b46] {
    display: block;
    position: absolute;
    top: 30px;
    right: 10px;
    font-size: 12px;
    color: #fff;
    background: #509dea;
    padding: 5px 10px;
    border-radius: 15px;
    z-index: 2;
}
.error-tip[data-v-df6a4b46] {
    position: relative;
    display: block;
    color: #a8a8a8;
    margin: 0 auto;
    top:100px;
    text-align: center;
    line-height: 34px;
    font-size: 14px;
    z-index: 3;
}
.video-play-icon[data-v-df6a4b46] {
    background: url(http://static.youku.com/h5/player/res/img/new_player_icons.png) no-repeat -200px 0px;
    z-index: 1;
    height: 70px;
    width: 75px;
    margin:  0 auto;
    padding-left: 10px;
    top: 50px;  
    position: relative;  
    background-size: 375px 100px;
   /* filter: grayscale(100%); *//*滤镜*/
    /*-webkit-filter: grayscale(100%);*/
}
/*播放结束*/

/*视频介绍*/
.introduce-video[data-v-df6a4b46] {
    height: 150px;
  /*    background-color: red;*/
    width: 100%;
    position: relative;
    border-bottom: 8px solid #e6e6e6;
}
.video-title[data-v-df6a4b46]{
    font-size: 18px;
    color: #333;
    top: 20px;
    position: absolute;
}
.glyphicon-chevron-down[data-v-df6a4b46] {
    margin-left: 20px;
    color: #999;
    font-size: 14px;
    top: 20px;
    position: absolute;
}
.playback[data-v-df6a4b46] {
    top: 50px;
    position: absolute;
}
.num[data-v-df6a4b46] {
    color:#fa533d;
    font-size: 18px;
}
.num-last[data-v-df6a4b46] {
    color:#fa533d;
    font-size: 12px;
}
.fen[data-v-df6a4b46] {
    color:#fa533d;
    font-size: 14px;
}
.play-num[data-v-df6a4b46] {
    margin-left: 10px;
    font-size: 14px;
    color: #999;
}
.left[data-v-df6a4b46] {
    float: left;
}
.like-new-icon[data-v-df6a4b46] {
    width: 95%;
    top: 80px;
    position: absolute;
    padding: 10px 5px 5px;
}
.like-icon[data-v-df6a4b46] {
    display: block;
    width: 80px;
    height: 40px;
    background: url(http://m.youku.com/video/images/gather.png?ver=1499329066389) no-repeat 0 -20px;
    background-size: 54px 1400px;
}
.like-num[data-v-df6a4b46] {
    margin-left: 25px;
    position: relative;
    top: 20px;
    font-size: 12px;
}
a span.like-num[data-v-df6a4b46], a span.type[data-v-df6a4b46] {
    color: #555;
}
.right[data-v-df6a4b46] {
    float: right;
}
.arrow[data-v-df6a4b46] {
    display: inline-block;
    width: 45px;
    height: 40px;
    margin-right: 10px;
    background: url(http://m.youku.com/video/images/gather.png?ver=1499329066389) no-repeat 0 -1270px;
    background-size: 54px 1400px;
}
.star[data-v-df6a4b46] {
    display: inline-block;
    width: 40px;
    height: 40px;
    margin-right: 10px;
    background: url(http://m.youku.com/video/images/gather.png?ver=1499329066389) no-repeat 0 -55px;
    background-size: 54px 1400px;
}
.type[data-v-df6a4b46] {
    border: 1px solid #555;
    border-radius: 5px;
    text-align: center;
    padding: 4px;
    font-size: 12px;
    position: relative;
    margin-right: 10px;
    top: -10px;
    left: -10px;
}
.forwarding[data-v-df6a4b46] {
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url(http://m.youku.com/video/images/gather.png?ver=1499329066389) no-repeat 0 -1310px;
    background-size: 54px 1400px;
}
/*视频介绍结束*/

/*选集*/
.choose-part[data-v-df6a4b46] {
    border-bottom: 8px solid #e6e6e6;
}
.part-title[data-v-df6a4b46] {
    position: relative;
    top:10px;
}
.part-num[data-v-df6a4b46] {
    position: relative;
    top:15px;
    color: #b5b7b9;
}
.clearfix[data-v-df6a4b46] {
    clear: both;
    zoom: 1;
}
.list-box[data-v-df6a4b46] {
   top:10px;
   position: relative; 
   width: 100%;
   white-space:nowrap;
   overflow-y: hidden; 
   overflow-x: scroll;
   margin-bottom: 6px;
}
.list-box[data-v-df6a4b46]::-webkit-scrollbar {
    display:none;
}
.book[data-v-df6a4b46] {
    background: url(http://m.youku.com/video/images/gather.png?ver=1499329066389) no-repeat 0px -990px;
    background-size: 54px 1400px;
    position: absolute;
    top: -6px;
    right: -10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
}
.slide-list ul[data-v-df6a4b46] {   
    width: 2888px;
    height: 65px;
    line-height: 65px;
}
.slide-list ul li a[data-v-df6a4b46] {
     color: #333;
}
.slide-list ul li[data-v-df6a4b46] {
    list-style: none;
    display: inline-block;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    border: 1px solid #ebebeb;
    line-height: 52px;
    text-align: center;
    margin-right: 8px;
    font-size: 14px;
    position: relative;
}
/*选集结束*/

/*明星列表*/
.slide-list2 ul li[data-v-df6a4b46] {
    display: inline-block;
    overflow: hidden;
    width: 82px;
    height: 94px;
    text-align: center;
}
.slide-list2 ul li a[data-v-df6a4b46] {
    display: inline-block;
    height: 94px;
    width: 54px;
    color: #333;
    text-decoration: none;
}
.slide-list2 ul li img[data-v-df6a4b46] {
    width: 54px;
    height: 54px;
   /* border: 1px solid #ebebeb;*/
    border-radius: 50%;
    overflow: hidden;
}
.slide-list2 ul li p[data-v-df6a4b46]{
    padding-top: 10px;
}
.actor[data-v-df6a4b46] {
    border-bottom: 8px solid #e6e6e6;
}
/*明星列表结束*/
ul li a.heighlight[data-v-df6a4b46] {
    color: #2692ff;
}

/*视频列表*/
.video-lists img[data-v-df6a4b46] {
    height: 206px;
    margin-top: 20px;
}
.imgs[data-v-df6a4b46] {
    padding-left: 10px;
    padding-right: 0;
    width: 100%;
}




